import React, { useEffect, useState } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { useNavigate, useParams } from 'react-router-dom'
import {
  ChartBarIcon,
  CubeIcon,
  PlayIcon,
  Cog6ToothIcon,
  ArrowTrendingUpIcon,
  ClockIcon
} from '@heroicons/react/24/outline'
import Interactive3D from '../components/Interactive3D'
import AnalyticsChart from '../components/AnalyticsChart'
import useStore from '../store/useStore'
import toast from 'react-hot-toast'

const Dashboard = ({ team: propTeam }) => {
  const navigate = useNavigate()
  const params = useParams()
  const {
    selectedTeam,
    setSelectedTeam,
    dashboardView,
    setDashboardView,
    analyticsData,
    setCurrentPage
  } = useStore()

  const [activeTab, setActiveTab] = useState('overview')
  const [isLoading, setIsLoading] = useState(false)
  const [lastUpdate, setLastUpdate] = useState(new Date())

  const currentTeam = propTeam || params.team || selectedTeam

  useEffect(() => {
    setCurrentPage('dashboard')
    if (currentTeam !== selectedTeam) {
      setSelectedTeam(currentTeam)
    }
  }, [currentTeam, selectedTeam, setSelectedTeam, setCurrentPage])

  // Update timestamp periodically
  useEffect(() => {
    const interval = setInterval(() => {
      setLastUpdate(new Date())
    }, 5000)

    return () => clearInterval(interval)
  }, [])

  const teams = {
    cardinals: {
      name: 'St. Louis Cardinals',
      sport: 'MLB',
      color: '#dc2626',
      bgColor: 'bg-red-600',
      data: analyticsData.cardinals
    },
    titans: {
      name: 'Tennessee Titans',
      sport: 'NFL',
      color: '#4A90E2',
      bgColor: 'bg-blue-600',
      data: analyticsData.titans
    },
    longhorns: {
      name: 'Texas Longhorns',
      sport: 'NCAA Football',
      color: '#BF5700',
      bgColor: 'bg-orange-600',
      data: analyticsData.longhorns
    },
    grizzlies: {
      name: 'Memphis Grizzlies',
      sport: 'NBA',
      color: '#00B2A9',
      bgColor: 'bg-teal-600',
      data: analyticsData.grizzlies
    }
  }

  const currentTeamData = teams[currentTeam] || teams.cardinals

  const handleTeamSwitch = (teamKey) => {
    setIsLoading(true)
    setSelectedTeam(teamKey)

    // Simulate loading time
    setTimeout(() => {
      setIsLoading(false)
      navigate(`/${teamKey}`)
      toast.success(`Switched to ${teams[teamKey].name}`)
    }, 500)
  }

  const handleViewChange = (view) => {
    setDashboardView(view)
    setActiveTab(view)
    toast.success(`Switched to ${view} view`)
  }

  const handleStartAnalysis = () => {
    setIsLoading(true)
    toast.loading('Starting advanced analysis...', { id: 'analysis' })

    setTimeout(() => {
      setIsLoading(false)
      toast.success('Analysis complete!', { id: 'analysis' })
      navigate('/analytics')
    }, 2000)
  }

  const dashboardTabs = [
    { key: 'overview', label: 'Overview', icon: ChartBarIcon },
    { key: '3d', label: '3D View', icon: CubeIcon },
    { key: 'live', label: 'Live Data', icon: PlayIcon },
    { key: 'settings', label: 'Settings', icon: Cog6ToothIcon }
  ]

  const renderMetricsCards = () => {
    const { data } = currentTeamData

    let metrics = []

    switch (currentTeam) {
      case 'cardinals':
        metrics = [
          { label: 'Batting Average', value: data.battingAvg || 0.000, format: '.3f' },
          { label: 'ERA', value: data.era || 0.00, format: '.2f' },
          { label: 'Wins', value: data.wins || 0, format: 'int' },
          { label: 'Losses', value: data.losses || 0, format: 'int' }
        ]
        break
      case 'titans':
        metrics = [
          { label: 'Points For', value: data.pointsFor || 0, format: 'int' },
          { label: 'Points Against', value: data.pointsAgainst || 0, format: 'int' },
          { label: 'Wins', value: data.wins || 0, format: 'int' },
          { label: 'Losses', value: data.losses || 0, format: 'int' }
        ]
        break
      case 'longhorns':
        metrics = [
          { label: 'Offensive Yards', value: data.offensiveYards || 0, format: 'int' },
          { label: 'Defensive Yards', value: data.defensiveYards || 0, format: 'int' },
          { label: 'Wins', value: data.wins || 0, format: 'int' },
          { label: 'Losses', value: data.losses || 0, format: 'int' }
        ]
        break
      case 'grizzlies':
        metrics = [
          { label: 'Points Per Game', value: data.pointsFor || 0, format: '.1f' },
          { label: 'Points Allowed', value: data.pointsAgainst || 0, format: '.1f' },
          { label: 'Wins', value: data.wins || 0, format: 'int' },
          { label: 'Losses', value: data.losses || 0, format: 'int' }
        ]
        break
    }

    return (
      <div className="grid grid-cols-2 lg:grid-cols-4 gap-4 mb-8">
        {metrics.map((metric, index) => (
          <motion.div
            key={metric.label}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: index * 0.1 }}
            className="card p-4"
          >
            <h3 className="text-sm font-medium text-gray-400 mb-1">{metric.label}</h3>
            <p className="text-2xl font-bold text-white">
              {metric.format === 'int'
                ? Math.floor(metric.value)
                : metric.format === '.1f'
                ? metric.value.toFixed(1)
                : metric.format === '.2f'
                ? metric.value.toFixed(2)
                : metric.value.toFixed(3)}
            </p>
          </motion.div>
        ))}
      </div>
    )
  }

  if (isLoading) {
    return (
      <div className="min-h-screen bg-black flex items-center justify-center">
        <div className="text-center">
          <div className="inline-block animate-spin rounded-full h-12 w-12 border-b-2 border-blaze-orange mb-4"></div>
          <p className="text-white text-lg">Loading {currentTeamData.name} Dashboard...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-black pt-20 pb-12">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          className="mb-8"
        >
          <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between">
            <div className="flex items-center space-x-4 mb-4 lg:mb-0">
              <div className={`w-12 h-12 ${currentTeamData.bgColor} rounded-lg flex items-center justify-center`}>
                <span className="text-white font-bold text-lg">
                  {currentTeamData.name.split(' ').map(word => word.charAt(0)).join('')}
                </span>
              </div>
              <div>
                <h1 className="text-3xl font-oswald font-bold text-white">
                  {currentTeamData.name}
                </h1>
                <p className="text-gray-400">{currentTeamData.sport} Analytics Dashboard</p>
              </div>
            </div>

            <div className="flex items-center space-x-4">
              <div className="flex items-center text-sm text-gray-400">
                <ClockIcon className="w-4 h-4 mr-1" />
                <span>Updated {lastUpdate.toLocaleTimeString()}</span>
              </div>

              <motion.button
                onClick={handleStartAnalysis}
                className="btn-primary flex items-center space-x-2"
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                <ArrowTrendingUpIcon className="w-4 h-4" />
                <span>Analyze</span>
              </motion.button>
            </div>
          </div>
        </motion.div>

        {/* Team Selection */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          className="mb-8"
        >
          <div className="flex space-x-2 overflow-x-auto pb-2">
            {Object.entries(teams).map(([key, team]) => (
              <motion.button
                key={key}
                onClick={() => handleTeamSwitch(key)}
                className={`flex items-center space-x-2 px-4 py-2 rounded-lg whitespace-nowrap transition-all duration-300 ${
                  currentTeam === key
                    ? `${team.bgColor} text-white`
                    : 'bg-gray-800 text-gray-300 hover:bg-gray-700'
                }`}
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
              >
                <span className="text-sm font-medium">{team.name}</span>
              </motion.button>
            ))}
          </div>
        </motion.div>

        {/* Dashboard Tabs */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          className="mb-8"
        >
          <div className="flex space-x-1 bg-gray-900 p-1 rounded-lg">
            {dashboardTabs.map((tab) => (
              <motion.button
                key={tab.key}
                onClick={() => handleViewChange(tab.key)}
                className={`flex items-center space-x-2 px-4 py-2 rounded-md transition-all duration-300 ${
                  activeTab === tab.key
                    ? 'bg-blaze-orange text-white'
                    : 'text-gray-300 hover:text-white hover:bg-gray-800'
                }`}
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
              >
                <tab.icon className="w-4 h-4" />
                <span className="text-sm font-medium">{tab.label}</span>
              </motion.button>
            ))}
          </div>
        </motion.div>

        {/* Dashboard Content */}
        <AnimatePresence mode="wait">
          {activeTab === 'overview' && (
            <motion.div
              key="overview"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
              transition={{ duration: 0.3 }}
            >
              {renderMetricsCards()}

              <div className="grid lg:grid-cols-2 gap-8 mb-8">
                <AnalyticsChart
                  type="line"
                  title="Performance Trends"
                  height={300}
                />
                <AnalyticsChart
                  type="bar"
                  title="Current Stats"
                  height={300}
                />
              </div>
            </motion.div>
          )}

          {activeTab === '3d' && (
            <motion.div
              key="3d"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
              transition={{ duration: 0.3 }}
            >
              <Interactive3D
                sport={currentTeam === 'cardinals' ? 'baseball' : currentTeam === 'grizzlies' ? 'basketball' : 'football'}
                className="mb-8"
              />
            </motion.div>
          )}

          {activeTab === 'live' && (
            <motion.div
              key="live"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
              transition={{ duration: 0.3 }}
            >
              <div className="grid md:grid-cols-2 gap-8 mb-8">
                <AnalyticsChart
                  type="radar"
                  title="Performance Radar"
                  height={300}
                />
                <AnalyticsChart
                  type="doughnut"
                  title="Season Breakdown"
                  height={300}
                />
              </div>
            </motion.div>
          )}

          {activeTab === 'settings' && (
            <motion.div
              key="settings"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
              transition={{ duration: 0.3 }}
            >
              <div className="card p-6">
                <h3 className="text-lg font-semibold text-white mb-4">Dashboard Settings</h3>
                <p className="text-gray-400 mb-4">
                  Configure your dashboard preferences and data sources.
                </p>
                <div className="space-y-4">
                  <div className="flex items-center justify-between">
                    <span className="text-white">Auto-refresh data</span>
                    <button className="bg-blaze-orange rounded-full w-12 h-6 relative">
                      <div className="bg-white rounded-full w-5 h-5 absolute right-0.5 top-0.5"></div>
                    </button>
                  </div>
                  <div className="flex items-center justify-between">
                    <span className="text-white">3D animations</span>
                    <button className="bg-blaze-orange rounded-full w-12 h-6 relative">
                      <div className="bg-white rounded-full w-5 h-5 absolute right-0.5 top-0.5"></div>
                    </button>
                  </div>
                  <div className="flex items-center justify-between">
                    <span className="text-white">Sound notifications</span>
                    <button className="bg-gray-600 rounded-full w-12 h-6 relative">
                      <div className="bg-white rounded-full w-5 h-5 absolute left-0.5 top-0.5"></div>
                    </button>
                  </div>
                </div>
              </div>
            </motion.div>
          )}
        </AnimatePresence>
      </div>
    </div>
  )
}

export default Dashboard