import React, { useEffect, useState } from 'react'
import { motion } from 'framer-motion'
import { ChartBarIcon, CpuChipIcon, EyeIcon } from '@heroicons/react/24/outline'
import AnalyticsChart from '../components/AnalyticsChart'
import Interactive3D from '../components/Interactive3D'
import useStore from '../store/useStore'

const Analytics = () => {
  const { setCurrentPage, analyticsData } = useStore()
  const [selectedChart, setSelectedChart] = useState('line')

  useEffect(() => {
    setCurrentPage('analytics')
  }, [setCurrentPage])

  const chartTypes = [
    { key: 'line', label: 'Line Chart', icon: ChartBarIcon },
    { key: 'bar', label: 'Bar Chart', icon: ChartBarIcon },
    { key: 'radar', label: 'Radar Chart', icon: CpuChipIcon },
    { key: 'doughnut', label: 'Doughnut', icon: EyeIcon }
  ]

  return (
    <div className="min-h-screen bg-black pt-20 pb-12">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          className="mb-8"
        >
          <h1 className="text-4xl font-oswald font-bold text-white mb-2">
            Advanced Analytics
          </h1>
          <p className="text-gray-400">
            Comprehensive data analysis and visualization platform
          </p>
        </motion.div>

        {/* Chart Type Selection */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          className="mb-8"
        >
          <div className="flex space-x-2 overflow-x-auto pb-2">
            {chartTypes.map((chart) => (
              <motion.button
                key={chart.key}
                onClick={() => setSelectedChart(chart.key)}
                className={`flex items-center space-x-2 px-4 py-2 rounded-lg whitespace-nowrap transition-all duration-300 ${
                  selectedChart === chart.key
                    ? 'bg-blaze-orange text-white'
                    : 'bg-gray-800 text-gray-300 hover:bg-gray-700'
                }`}
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
              >
                <chart.icon className="w-4 h-4" />
                <span className="text-sm font-medium">{chart.label}</span>
              </motion.button>
            ))}
          </div>
        </motion.div>

        {/* 3D Visualization */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          className="mb-8"
        >
          <Interactive3D sport="overview" />
        </motion.div>

        {/* Analytics Charts Grid */}
        <div className="grid md:grid-cols-2 gap-8 mb-8">
          <motion.div
            initial={{ opacity: 0, x: -20 }}
            animate={{ opacity: 1, x: 0 }}
          >
            <AnalyticsChart
              type={selectedChart}
              title={`${chartTypes.find(c => c.key === selectedChart)?.label} Analysis`}
              height={400}
            />
          </motion.div>

          <motion.div
            initial={{ opacity: 0, x: 20 }}
            animate={{ opacity: 1, x: 0 }}
          >
            <AnalyticsChart
              type="radar"
              title="Team Performance Radar"
              height={400}
            />
          </motion.div>
        </div>

        <div className="grid md:grid-cols-3 gap-8">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.1 }}
          >
            <AnalyticsChart
              type="doughnut"
              title="Season Distribution"
              height={300}
            />
          </motion.div>

          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.2 }}
          >
            <AnalyticsChart
              type="bar"
              title="Current Rankings"
              height={300}
            />
          </motion.div>

          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.3 }}
            className="card p-6"
          >
            <h3 className="text-lg font-semibold text-white mb-4">Key Insights</h3>
            <ul className="space-y-3 text-sm text-gray-300">
              <li className="flex items-start space-x-2">
                <span className="text-blaze-orange"></span>
                <span>Cardinals showing improved batting consistency</span>
              </li>
              <li className="flex items-start space-x-2">
                <span className="text-cardinal-blue"></span>
                <span>Titans defense trending upward this quarter</span>
              </li>
              <li className="flex items-start space-x-2">
                <span className="text-grizzly-teal"></span>
                <span>Longhorns offense maintaining high efficiency</span>
              </li>
              <li className="flex items-start space-x-2">
                <span className="text-yellow-500"></span>
                <span>Grizzlies showing balanced performance</span>
              </li>
            </ul>
          </motion.div>
        </div>
      </div>
    </div>
  )
}

export default Analytics