import React, { useState, useEffect } from 'react'
import { Link, useLocation, useNavigate } from 'react-router-dom'
import { motion, AnimatePresence } from 'framer-motion'
import { Bars3Icon, XMarkIcon, ChartBarIcon, CpuChipIcon } from '@heroicons/react/24/outline'
import useStore from '../store/useStore'

const Header = () => {
  const location = useLocation()
  const navigate = useNavigate()
  const { mobileMenuOpen, toggleMobileMenu, closeMobileMenu, setCurrentPage } = useStore()
  const [scrolled, setScrolled] = useState(false)

  // Handle scroll effect
  useEffect(() => {
    const handleScroll = () => {
      const isScrolled = window.scrollY > 20
      setScrolled(isScrolled)
    }

    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  // Close mobile menu on route change
  useEffect(() => {
    closeMobileMenu()
    // Update current page in store
    const path = location.pathname.replace('/', '') || 'home'
    setCurrentPage(path)
  }, [location.pathname, closeMobileMenu, setCurrentPage])

  const handleNavClick = (path, label) => {
    navigate(path)
    closeMobileMenu()
    setCurrentPage(label)
  }

  const navigationItems = [
    { path: '/', label: 'Home', name: 'home' },
    { path: '/dashboard', label: 'Dashboard', name: 'dashboard', icon: ChartBarIcon },
    { path: '/analytics', label: 'Analytics', name: 'analytics', icon: CpuChipIcon },
    { path: '/live-scores', label: 'Live Scores', name: 'live-scores' },
    { path: '/about', label: 'About', name: 'about' },
    { path: '/contact', label: 'Contact', name: 'contact' }
  ]

  const teamNavigation = [
    { path: '/cardinals', label: 'Cardinals', team: 'cardinals', color: 'text-red-500' },
    { path: '/titans', label: 'Titans', team: 'titans', color: 'text-blue-400' },
    { path: '/longhorns', label: 'Longhorns', team: 'longhorns', color: 'text-blaze-orange' },
    { path: '/grizzlies', label: 'Grizzlies', team: 'grizzlies', color: 'text-grizzly-teal' }
  ]

  const isActiveLink = (path) => {
    if (path === '/') return location.pathname === '/'
    return location.pathname.startsWith(path)
  }

  return (
    <>
      {/* Header */}
      <motion.header
        initial={{ y: -100 }}
        animate={{ y: 0 }}
        className={`fixed top-0 left-0 right-0 z-50 transition-all duration-300 ${
          scrolled
            ? 'bg-black/90 backdrop-blur-md shadow-lg'
            : 'bg-transparent'
        }`}
      >
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            {/* Logo */}
            <motion.div
              whileHover={{ scale: 1.05 }}
              className="flex-shrink-0"
            >
              <button
                onClick={() => handleNavClick('/', 'home')}
                className="flex items-center space-x-2 text-white hover:text-blaze-orange transition-colors duration-300"
              >
                <div className="w-8 h-8 bg-gradient-blaze rounded flex items-center justify-center">
                  <span className="text-white font-bold text-sm">BI</span>
                </div>
                <span className="font-oswald font-bold text-lg hidden sm:inline">
                  BLAZE INTELLIGENCE
                </span>
              </button>
            </motion.div>

            {/* Desktop Navigation */}
            <nav className="hidden lg:flex items-center space-x-8">
              {navigationItems.map((item) => {
                const Icon = item.icon
                return (
                  <motion.button
                    key={item.name}
                    onClick={() => handleNavClick(item.path, item.name)}
                    className={`flex items-center space-x-1 px-3 py-2 rounded-md text-sm font-medium transition-all duration-300 ${
                      isActiveLink(item.path)
                        ? 'text-blaze-orange bg-blaze-orange/10'
                        : 'text-gray-300 hover:text-white hover:bg-gray-800/50'
                    }`}
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                  >
                    {Icon && <Icon className="w-4 h-4" />}
                    <span>{item.label}</span>
                  </motion.button>
                )
              })}
            </nav>

            {/* Team Quick Access (Desktop) */}
            <div className="hidden xl:flex items-center space-x-2">
              {teamNavigation.map((team) => (
                <motion.button
                  key={team.team}
                  onClick={() => handleNavClick(team.path, team.team)}
                  className={`px-2 py-1 text-xs font-medium rounded transition-all duration-300 ${
                    isActiveLink(team.path)
                      ? `${team.color} bg-current/10`
                      : 'text-gray-400 hover:text-white hover:bg-gray-800/50'
                  }`}
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                >
                  {team.label}
                </motion.button>
              ))}
            </div>

            {/* Mobile menu button */}
            <motion.button
              onClick={toggleMobileMenu}
              className="lg:hidden inline-flex items-center justify-center p-2 rounded-md text-gray-300 hover:text-white hover:bg-gray-800/50 transition-colors duration-300"
              whileTap={{ scale: 0.95 }}
            >
              <span className="sr-only">Open main menu</span>
              {mobileMenuOpen ? (
                <XMarkIcon className="block h-6 w-6" />
              ) : (
                <Bars3Icon className="block h-6 w-6" />
              )}
            </motion.button>
          </div>
        </div>
      </motion.header>

      {/* Mobile Navigation */}
      <AnimatePresence>
        {mobileMenuOpen && (
          <>
            {/* Backdrop */}
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
              onClick={closeMobileMenu}
              className="fixed inset-0 bg-black/50 backdrop-blur-sm z-40 lg:hidden"
            />

            {/* Menu Panel */}
            <motion.div
              initial={{ x: '100%' }}
              animate={{ x: 0 }}
              exit={{ x: '100%' }}
              transition={{ type: 'tween', duration: 0.3 }}
              className="fixed top-0 right-0 bottom-0 w-80 bg-gray-900 shadow-xl z-50 lg:hidden"
            >
              <div className="flex flex-col h-full">
                {/* Header */}
                <div className="flex items-center justify-between p-4 border-b border-gray-800">
                  <h2 className="text-lg font-oswald font-bold text-white">
                    NAVIGATION
                  </h2>
                  <button
                    onClick={closeMobileMenu}
                    className="p-2 rounded-md text-gray-300 hover:text-white hover:bg-gray-800 transition-colors duration-300"
                  >
                    <XMarkIcon className="h-5 w-5" />
                  </button>
                </div>

                {/* Main Navigation */}
                <div className="flex-1 py-4">
                  <nav className="space-y-1 px-4">
                    {navigationItems.map((item) => {
                      const Icon = item.icon
                      return (
                        <button
                          key={item.name}
                          onClick={() => handleNavClick(item.path, item.name)}
                          className={`w-full flex items-center space-x-3 px-4 py-3 rounded-lg text-left transition-all duration-300 ${
                            isActiveLink(item.path)
                              ? 'text-blaze-orange bg-blaze-orange/10 border border-blaze-orange/30'
                              : 'text-gray-300 hover:text-white hover:bg-gray-800/50'
                          }`}
                        >
                          {Icon && <Icon className="w-5 h-5" />}
                          <span className="font-medium">{item.label}</span>
                        </button>
                      )
                    })}
                  </nav>

                  {/* Team Navigation */}
                  <div className="mt-8 px-4">
                    <h3 className="text-xs font-semibold text-gray-400 uppercase tracking-wider mb-3">
                      Teams
                    </h3>
                    <div className="grid grid-cols-2 gap-2">
                      {teamNavigation.map((team) => (
                        <button
                          key={team.team}
                          onClick={() => handleNavClick(team.path, team.team)}
                          className={`px-3 py-2 text-sm font-medium rounded-md transition-all duration-300 ${
                            isActiveLink(team.path)
                              ? `${team.color} bg-current/10`
                              : 'text-gray-400 hover:text-white hover:bg-gray-800/50'
                          }`}
                        >
                          {team.label}
                        </button>
                      ))}
                    </div>
                  </div>
                </div>

                {/* Footer */}
                <div className="p-4 border-t border-gray-800">
                  <p className="text-xs text-gray-400 text-center">
                    Blaze Intelligence  2025
                  </p>
                </div>
              </div>
            </motion.div>
          </>
        )}
      </AnimatePresence>
    </>
  )
}

export default Header