import React, { useRef, useEffect, useState } from 'react'
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  BarElement,
  ArcElement,
  RadialLinearScale
} from 'chart.js'
import { Line, Bar, Doughnut, Radar } from 'react-chartjs-2'
import { motion } from 'framer-motion'
import useStore from '../store/useStore'

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  BarElement,
  ArcElement,
  RadialLinearScale
)

const AnalyticsChart = ({ type = 'line', sport = 'all', height = 300, title }) => {
  const chartRef = useRef(null)
  const { analyticsData } = useStore()
  const [timeData, setTimeData] = useState([])
  const [animationComplete, setAnimationComplete] = useState(false)

  // Generate time-series data for demonstration
  useEffect(() => {
    const generateTimeSeriesData = () => {
      const data = []
      const now = new Date()

      for (let i = 29; i >= 0; i--) {
        const date = new Date(now.getTime() - i * 24 * 60 * 60 * 1000)
        data.push({
          date: date.toLocaleDateString(),
          cardinals: Math.random() * 0.300 + 0.250,
          titans: Math.random() * 30 + 10,
          longhorns: Math.random() * 40 + 20,
          grizzlies: Math.random() * 120 + 90
        })
      }

      setTimeData(data)
    }

    generateTimeSeriesData()

    // Update data every 10 seconds for live effect
    const interval = setInterval(generateTimeSeriesData, 10000)
    return () => clearInterval(interval)
  }, [])

  // Chart configuration based on sport and type
  const getChartData = () => {
    const colors = {
      cardinals: '#dc2626',
      titans: '#4A90E2',
      longhorns: '#BF5700',
      grizzlies: '#00B2A9'
    }

    const gradients = {
      cardinals: null,
      titans: null,
      longhorns: null,
      grizzlies: null
    }

    // Create gradients if chart is available
    if (chartRef.current) {
      const ctx = chartRef.current.canvas.getContext('2d')
      Object.keys(colors).forEach(team => {
        const gradient = ctx.createLinearGradient(0, 0, 0, height)
        gradient.addColorStop(0, colors[team] + '80')
        gradient.addColorStop(1, colors[team] + '10')
        gradients[team] = gradient
      })
    }

    switch (type) {
      case 'line':
        return {
          labels: timeData.map(d => d.date.split('/').slice(0, 2).join('/')),
          datasets: [
            {
              label: 'Cardinals Performance',
              data: timeData.map(d => d.cardinals * 100),
              borderColor: colors.cardinals,
              backgroundColor: gradients.cardinals || colors.cardinals + '20',
              fill: true,
              tension: 0.4,
              pointBackgroundColor: colors.cardinals,
              pointBorderColor: '#ffffff',
              pointBorderWidth: 2,
              pointRadius: 4,
              pointHoverRadius: 6
            },
            {
              label: 'Titans Score',
              data: timeData.map(d => d.titans),
              borderColor: colors.titans,
              backgroundColor: gradients.titans || colors.titans + '20',
              fill: true,
              tension: 0.4,
              pointBackgroundColor: colors.titans,
              pointBorderColor: '#ffffff',
              pointBorderWidth: 2,
              pointRadius: 4,
              pointHoverRadius: 6
            }
          ]
        }

      case 'bar':
        return {
          labels: ['Cardinals', 'Titans', 'Longhorns', 'Grizzlies'],
          datasets: [
            {
              label: 'Current Performance',
              data: [
                analyticsData.cardinals.battingAvg * 1000,
                analyticsData.titans.pointsFor,
                analyticsData.longhorns.offensiveYards / 10,
                analyticsData.grizzlies.pointsFor
              ],
              backgroundColor: [
                colors.cardinals + '80',
                colors.titans + '80',
                colors.longhorns + '80',
                colors.grizzlies + '80'
              ],
              borderColor: [
                colors.cardinals,
                colors.titans,
                colors.longhorns,
                colors.grizzlies
              ],
              borderWidth: 2,
              borderRadius: 8,
              borderSkipped: false,
            }
          ]
        }

      case 'doughnut':
        return {
          labels: ['Cardinals', 'Titans', 'Longhorns', 'Grizzlies'],
          datasets: [
            {
              data: [25, 30, 25, 20],
              backgroundColor: [
                colors.cardinals,
                colors.titans,
                colors.longhorns,
                colors.grizzlies
              ],
              borderColor: '#1a1a1a',
              borderWidth: 4,
              hoverBorderWidth: 6
            }
          ]
        }

      case 'radar':
        return {
          labels: ['Offense', 'Defense', 'Consistency', 'Momentum', 'Overall'],
          datasets: [
            {
              label: 'Team Performance',
              data: [85, 72, 90, 65, 78],
              backgroundColor: colors.cardinals + '20',
              borderColor: colors.cardinals,
              pointBackgroundColor: colors.cardinals,
              pointBorderColor: '#ffffff',
              pointHoverRadius: 8
            }
          ]
        }

      default:
        return { labels: [], datasets: [] }
    }
  }

  const getChartOptions = () => {
    const baseOptions = {
      responsive: true,
      maintainAspectRatio: false,
      animation: {
        duration: animationComplete ? 0 : 2000,
        easing: 'easeInOutQuart',
        onComplete: () => setAnimationComplete(true)
      },
      plugins: {
        legend: {
          position: 'top',
          labels: {
            color: '#ffffff',
            font: {
              family: 'Inter, sans-serif',
              size: 12
            },
            usePointStyle: true,
            padding: 20
          }
        },
        tooltip: {
          backgroundColor: '#1a1a1a',
          titleColor: '#ffffff',
          bodyColor: '#ffffff',
          borderColor: '#BF5700',
          borderWidth: 1,
          cornerRadius: 8,
          displayColors: true
        }
      },
      scales: type === 'radar' ? {
        r: {
          angleLines: {
            color: '#374151'
          },
          grid: {
            color: '#374151'
          },
          ticks: {
            color: '#9CA3AF',
            backdropColor: 'transparent'
          }
        }
      } : {
        x: {
          grid: {
            color: '#374151',
            drawOnChartArea: true,
            drawTicks: true
          },
          ticks: {
            color: '#9CA3AF',
            font: {
              family: 'Inter, sans-serif',
              size: 11
            }
          }
        },
        y: {
          grid: {
            color: '#374151',
            drawOnChartArea: true,
            drawTicks: true
          },
          ticks: {
            color: '#9CA3AF',
            font: {
              family: 'Inter, sans-serif',
              size: 11
            }
          }
        }
      }
    }

    // Add specific options for different chart types
    if (type === 'doughnut') {
      baseOptions.cutout = '60%'
      baseOptions.plugins.legend.position = 'right'
    }

    return baseOptions
  }

  const renderChart = () => {
    const data = getChartData()
    const options = getChartOptions()

    switch (type) {
      case 'line':
        return <Line ref={chartRef} data={data} options={options} />
      case 'bar':
        return <Bar ref={chartRef} data={data} options={options} />
      case 'doughnut':
        return <Doughnut ref={chartRef} data={data} options={options} />
      case 'radar':
        return <Radar ref={chartRef} data={data} options={options} />
      default:
        return <Line ref={chartRef} data={data} options={options} />
    }
  }

  if (timeData.length === 0) {
    return (
      <div className="flex items-center justify-center h-64 bg-gray-900 rounded-lg">
        <div className="text-center">
          <div className="inline-block animate-spin rounded-full h-8 w-8 border-b-2 border-blaze-orange"></div>
          <p className="mt-2 text-gray-400">Loading chart data...</p>
        </div>
      </div>
    )
  }

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
      className="bg-gray-900 rounded-lg border border-gray-800 p-6"
    >
      {title && (
        <div className="mb-4">
          <h3 className="text-lg font-semibold text-white">{title}</h3>
          <div className="flex items-center mt-2">
            <div className="w-2 h-2 bg-green-500 rounded-full animate-pulse mr-2"></div>
            <span className="text-sm text-gray-400">Live Data</span>
          </div>
        </div>
      )}

      <div style={{ height: `${height}px` }}>
        {renderChart()}
      </div>

      {/* Chart Controls */}
      <div className="mt-4 flex items-center justify-between">
        <div className="flex space-x-2">
          <span className="text-xs text-gray-400">Last updated:</span>
          <span className="text-xs text-blaze-orange">
            {new Date().toLocaleTimeString()}
          </span>
        </div>

        <div className="flex items-center space-x-2">
          <div className="w-1 h-1 bg-blaze-orange rounded-full animate-pulse"></div>
          <span className="text-xs text-gray-400">Auto-refresh enabled</span>
        </div>
      </div>
    </motion.div>
  )
}

export default AnalyticsChart