import { create } from 'zustand'
import { subscribeWithSelector } from 'zustand/middleware'

const useStore = create(
  subscribeWithSelector((set, get) => ({
    // App State
    isLoading: false,
    error: null,
    currentPage: 'home',

    // Navigation State
    mobileMenuOpen: false,
    activeSection: 'home',

    // Dashboard State
    selectedTeam: 'cardinals',
    selectedSport: 'mlb',
    dashboardView: 'overview',

    // Analytics Data
    liveScores: [],
    analyticsData: {
      cardinals: {
        wins: 0,
        losses: 0,
        battingAvg: 0,
        era: 0,
        lastUpdated: null
      },
      titans: {
        wins: 0,
        losses: 0,
        pointsFor: 0,
        pointsAgainst: 0,
        lastUpdated: null
      },
      longhorns: {
        wins: 0,
        losses: 0,
        offensiveYards: 0,
        defensiveYards: 0,
        lastUpdated: null
      },
      grizzlies: {
        wins: 0,
        losses: 0,
        pointsFor: 0,
        pointsAgainst: 0,
        lastUpdated: null
      }
    },

    // Real-time Data Simulation
    dataUpdateInterval: null,

    // Actions
    setLoading: (loading) => set({ isLoading: loading }),

    setError: (error) => set({ error }),

    clearError: () => set({ error: null }),

    setCurrentPage: (page) => set({
      currentPage: page,
      activeSection: page
    }),

    toggleMobileMenu: () => set((state) => ({
      mobileMenuOpen: !state.mobileMenuOpen
    })),

    closeMobileMenu: () => set({ mobileMenuOpen: false }),

    setSelectedTeam: (team) => set({ selectedTeam: team }),

    setSelectedSport: (sport) => set({ selectedSport: sport }),

    setDashboardView: (view) => set({ dashboardView: view }),

    updateLiveScores: (scores) => set({ liveScores: scores }),

    updateAnalyticsData: (team, data) => set((state) => ({
      analyticsData: {
        ...state.analyticsData,
        [team]: {
          ...state.analyticsData[team],
          ...data,
          lastUpdated: new Date().toISOString()
        }
      }
    })),

    // Simulate real-time data updates
    startDataSimulation: () => {
      const interval = setInterval(() => {
        const teams = ['cardinals', 'titans', 'longhorns', 'grizzlies'];
        const randomTeam = teams[Math.floor(Math.random() * teams.length)];

        // Generate random data updates based on team
        let updateData = {};

        switch (randomTeam) {
          case 'cardinals':
            updateData = {
              battingAvg: (0.250 + Math.random() * 0.100).toFixed(3),
              era: (3.50 + Math.random() * 2.00).toFixed(2)
            };
            break;
          case 'titans':
            updateData = {
              pointsFor: Math.floor(20 + Math.random() * 15),
              pointsAgainst: Math.floor(15 + Math.random() * 20)
            };
            break;
          case 'longhorns':
            updateData = {
              offensiveYards: Math.floor(350 + Math.random() * 150),
              defensiveYards: Math.floor(250 + Math.random() * 100)
            };
            break;
          case 'grizzlies':
            updateData = {
              pointsFor: Math.floor(100 + Math.random() * 20),
              pointsAgainst: Math.floor(90 + Math.random() * 25)
            };
            break;
        }

        get().updateAnalyticsData(randomTeam, updateData);
      }, 5000); // Update every 5 seconds

      set({ dataUpdateInterval: interval });
    },

    stopDataSimulation: () => {
      const { dataUpdateInterval } = get();
      if (dataUpdateInterval) {
        clearInterval(dataUpdateInterval);
        set({ dataUpdateInterval: null });
      }
    },

    // Initialize app data
    initializeApp: async () => {
      set({ isLoading: true, error: null });

      try {
        // Simulate API call
        await new Promise(resolve => setTimeout(resolve, 1000));

        // Initialize with sample data
        const initialData = {
          cardinals: { wins: 82, losses: 80, battingAvg: 0.265, era: 4.12 },
          titans: { wins: 9, losses: 8, pointsFor: 312, pointsAgainst: 298 },
          longhorns: { wins: 10, losses: 3, offensiveYards: 445, defensiveYards: 278 },
          grizzlies: { wins: 45, losses: 37, pointsFor: 112.4, pointsAgainst: 108.7 }
        };

        Object.entries(initialData).forEach(([team, data]) => {
          get().updateAnalyticsData(team, data);
        });

        // Start real-time simulation
        get().startDataSimulation();

        set({ isLoading: false });
      } catch (error) {
        set({ isLoading: false, error: error.message });
      }
    }
  }))
)

export default useStore