import React from 'react'
import { motion } from 'framer-motion'

const LoadingSpinner = ({ size = 'large', text = 'Loading Blaze Intelligence...' }) => {
  const sizeClasses = {
    small: 'w-8 h-8',
    medium: 'w-12 h-12',
    large: 'w-16 h-16'
  }

  return (
    <div className="min-h-screen bg-black flex flex-col items-center justify-center">
      {/* Logo with animation */}
      <motion.div
        animate={{
          scale: [1, 1.2, 1],
          rotate: [0, 180, 360],
        }}
        transition={{
          duration: 2,
          repeat: Infinity,
          ease: "easeInOut"
        }}
        className={`${sizeClasses[size]} bg-gradient-blaze rounded-lg flex items-center justify-center mb-8`}
      >
        <span className="text-white font-bold text-lg">BI</span>
      </motion.div>

      {/* Loading dots */}
      <div className="flex space-x-2 mb-4">
        {[0, 1, 2].map((index) => (
          <motion.div
            key={index}
            animate={{
              scale: [1, 1.5, 1],
              opacity: [0.3, 1, 0.3],
            }}
            transition={{
              duration: 1.5,
              repeat: Infinity,
              delay: index * 0.2,
              ease: "easeInOut"
            }}
            className="w-2 h-2 bg-blaze-orange rounded-full"
          />
        ))}
      </div>

      {/* Loading text */}
      <motion.p
        animate={{ opacity: [0.5, 1, 0.5] }}
        transition={{ duration: 2, repeat: Infinity }}
        className="text-gray-400 text-sm font-medium"
      >
        {text}
      </motion.p>

      {/* Progress bar */}
      <div className="w-64 h-1 bg-gray-800 rounded-full mt-4 overflow-hidden">
        <motion.div
          animate={{ x: ['-100%', '100%'] }}
          transition={{ duration: 2, repeat: Infinity, ease: "easeInOut" }}
          className="h-full w-1/3 bg-gradient-blaze rounded-full"
        />
      </div>
    </div>
  )
}

export default LoadingSpinner