import React, { useEffect } from 'react'
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom'
import { Toaster } from 'react-hot-toast'

// Components
import Header from './components/Header'
import Footer from './components/Footer'
import LoadingSpinner from './components/LoadingSpinner'
import ErrorBoundary from './components/ErrorBoundary'

// Pages
import Home from './pages/Home'
import Dashboard from './pages/Dashboard'
import Analytics from './pages/Analytics'
import LiveScores from './pages/LiveScores'
import About from './pages/About'
import Contact from './pages/Contact'

// Store
import useStore from './store/useStore'

function App() {
  const { isLoading, error, initializeApp } = useStore()

  useEffect(() => {
    initializeApp()

    // Cleanup on unmount
    return () => {
      const { stopDataSimulation } = useStore.getState()
      stopDataSimulation()
    }
  }, [initializeApp])

  if (isLoading) {
    return <LoadingSpinner />
  }

  if (error) {
    return (
      <div className="min-h-screen bg-black flex items-center justify-center">
        <div className="text-center">
          <h1 className="text-2xl font-bold text-red-500 mb-4">Application Error</h1>
          <p className="text-gray-300 mb-4">{error}</p>
          <button
            onClick={() => window.location.reload()}
            className="btn-primary"
          >
            Reload Application
          </button>
        </div>
      </div>
    )
  }

  return (
    <ErrorBoundary>
      <Router>
        <div className="min-h-screen bg-black text-white">
          <Header />

          <main className="relative">
            <Routes>
              <Route path="/" element={<Home />} />
              <Route path="/dashboard" element={<Dashboard />} />
              <Route path="/analytics" element={<Analytics />} />
              <Route path="/live-scores" element={<LiveScores />} />
              <Route path="/about" element={<About />} />
              <Route path="/contact" element={<Contact />} />

              {/* Team-specific routes */}
              <Route path="/cardinals" element={<Dashboard team="cardinals" />} />
              <Route path="/titans" element={<Dashboard team="titans" />} />
              <Route path="/longhorns" element={<Dashboard team="longhorns" />} />
              <Route path="/grizzlies" element={<Dashboard team="grizzlies" />} />

              {/* Redirect old paths */}
              <Route path="/dashboard.html" element={<Navigate to="/dashboard" replace />} />
              <Route path="/analytics-dashboard.html" element={<Navigate to="/analytics" replace />} />
              <Route path="/live-scores.html" element={<Navigate to="/live-scores" replace />} />

              {/* 404 fallback */}
              <Route path="*" element={<Navigate to="/" replace />} />
            </Routes>
          </main>

          <Footer />

          {/* Toast notifications */}
          <Toaster
            position="top-right"
            toastOptions={{
              duration: 4000,
              style: {
                background: '#1a1a1a',
                color: '#fff',
                border: '1px solid #BF5700'
              },
              success: {
                iconTheme: {
                  primary: '#BF5700',
                  secondary: '#fff'
                }
              },
              error: {
                iconTheme: {
                  primary: '#ef4444',
                  secondary: '#fff'
                }
              }
            }}
          />
        </div>
      </Router>
    </ErrorBoundary>
  )
}

export default App