import React, { useEffect, useState } from 'react'
import { motion } from 'framer-motion'
import { PlayIcon, PauseIcon, TvIcon } from '@heroicons/react/24/outline'
import useStore from '../store/useStore'

const LiveScores = () => {
  const { setCurrentPage, liveScores, updateLiveScores } = useStore()
  const [isLive, setIsLive] = useState(true)

  useEffect(() => {
    setCurrentPage('live-scores')

    // Generate mock live scores
    const generateMockScores = () => {
      const games = [
        {
          id: 1,
          homeTeam: 'Cardinals',
          awayTeam: 'Cubs',
          homeScore: Math.floor(Math.random() * 10),
          awayScore: Math.floor(Math.random() * 10),
          inning: '7th',
          status: 'Live'
        },
        {
          id: 2,
          homeTeam: 'Titans',
          awayTeam: 'Colts',
          homeScore: Math.floor(Math.random() * 35),
          awayScore: Math.floor(Math.random() * 35),
          quarter: '3rd',
          status: 'Live'
        },
        {
          id: 3,
          homeTeam: 'Longhorns',
          awayTeam: 'Sooners',
          homeScore: Math.floor(Math.random() * 42),
          awayScore: Math.floor(Math.random() * 42),
          quarter: '2nd',
          status: 'Live'
        },
        {
          id: 4,
          homeTeam: 'Grizzlies',
          awayTeam: 'Lakers',
          homeScore: Math.floor(Math.random() * 130),
          awayScore: Math.floor(Math.random() * 130),
          quarter: '4th',
          status: 'Live'
        }
      ]

      updateLiveScores(games)
    }

    generateMockScores()

    let interval
    if (isLive) {
      interval = setInterval(generateMockScores, 3000)
    }

    return () => {
      if (interval) clearInterval(interval)
    }
  }, [setCurrentPage, updateLiveScores, isLive])

  return (
    <div className="min-h-screen bg-black pt-20 pb-12">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          className="mb-8"
        >
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-4xl font-oswald font-bold text-white mb-2">
                Live Scores
              </h1>
              <p className="text-gray-400">
                Real-time sports scores and updates
              </p>
            </div>

            <motion.button
              onClick={() => setIsLive(!isLive)}
              className={`flex items-center space-x-2 px-4 py-2 rounded-lg transition-colors ${
                isLive ? 'bg-green-600 text-white' : 'bg-gray-600 text-gray-300'
              }`}
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              {isLive ? <PauseIcon className="w-4 h-4" /> : <PlayIcon className="w-4 h-4" />}
              <span>{isLive ? 'Live' : 'Paused'}</span>
            </motion.button>
          </div>
        </motion.div>

        {/* Live Scores Grid */}
        <div className="grid md:grid-cols-2 lg:grid-cols-2 gap-6">
          {liveScores.map((game) => (
            <motion.div
              key={game.id}
              initial={{ opacity: 0, scale: 0.95 }}
              animate={{ opacity: 1, scale: 1 }}
              className="card p-6 relative overflow-hidden"
            >
              {isLive && (
                <div className="absolute top-4 right-4">
                  <div className="flex items-center space-x-1">
                    <div className="w-2 h-2 bg-red-500 rounded-full animate-pulse"></div>
                    <span className="text-xs text-red-500 font-medium">LIVE</span>
                  </div>
                </div>
              )}

              <div className="flex items-center justify-between mb-4">
                <div className="text-center flex-1">
                  <h3 className="text-lg font-semibold text-white mb-1">{game.awayTeam}</h3>
                  <p className="text-3xl font-bold text-blaze-orange">{game.awayScore}</p>
                </div>

                <div className="text-center mx-4">
                  <div className="text-2xl text-gray-400">vs</div>
                  <div className="text-sm text-gray-500 mt-1">
                    {game.inning || game.quarter}
                  </div>
                </div>

                <div className="text-center flex-1">
                  <h3 className="text-lg font-semibold text-white mb-1">{game.homeTeam}</h3>
                  <p className="text-3xl font-bold text-blaze-orange">{game.homeScore}</p>
                </div>
              </div>

              <div className="flex items-center justify-center space-x-4 text-sm text-gray-400">
                <div className="flex items-center space-x-1">
                  <TvIcon className="w-4 h-4" />
                  <span>Watch Live</span>
                </div>
                <span></span>
                <span>{game.status}</span>
              </div>
            </motion.div>
          ))}
        </div>

        {liveScores.length === 0 && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            className="text-center py-12"
          >
            <TvIcon className="w-12 h-12 text-gray-600 mx-auto mb-4" />
            <p className="text-gray-400">No live games at the moment</p>
          </motion.div>
        )}
      </div>
    </div>
  )
}

export default LiveScores